import React from "react";
import Swal from "sweetalert2";
import AutorenewIcon from "@material-ui/icons/Autorenew";
import { FarmServices } from "../../../Services/FarmServices";
import AddOrUpdatePrices from "./AddOrUpdatePrices";

const Settings = () => {
  const resetCycle = async () => {
    Swal.fire({
      title: "  ",
      text: "     !!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "",
      cancelButtonText: "",
      reverseButtons: true,
    }).then(async (result) => {
      if (result.isConfirmed) {
        {
          const res = await FarmServices.ResetFarmCycle();
          if (res == "")
            Swal.fire({
              icon: "success",
              title: "   ",
              showConfirmButton: false,
              timer: 2000,
            });
          else {
            debugger;
            Swal.fire({
              icon: "error",
              title: "  ",
              text: res,
            });
          }
        }
      }
    });
  };

  return (
    <div>
      <AddOrUpdatePrices />

      <div className="card" style={{ marginTop: "20px" }} >
        <div className="card-header card-header-text card-header-primary">
          <div className="card-text">
            <h4 className="card-title" style={{ textAlign: "center" }}>
                
            </h4>
          </div>
        </div>
        <div className="card-body">
          <div className="card">
            <div className="row" style={{ margin: "20px" }}>
              <div className="col-md-2"></div>

              <div className="col-md-8">
                <button
                  className="btn btn-primary btn-round"
                  style={{ alignSelf: "center", width: "100%" , backgroundColor : "#eb3443"}}
                  onClick={resetCycle}
                >
                  <AutorenewIcon />{" "}
                  <label
                    style={{
                      color: "white",
                      fontWeight: 900,
                      fontSize: "125%",
                    }}
                  >
                      
                  </label>
                </button>
              </div>

              <div className="col-md-2"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Settings;
